<script>
import $ from 'webpack-zepto'
export default {
  name: 'util',
  methods: {
    // 只传一个data，会迭代出里面所有的内容对未定义和‘’做判断，并弹出键值名提醒
    vali: function (data, type) {
      let that = this
      if (type !== undefined) {
        switch (type) {
          case 'phone':
            return /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(data)
          case 'number':
            return /^\d+/.test(data)
        }
      } else {
        $.each(data, (key, value) => {
          // console.log(value)
          if ((typeof (value) === 'string' && value === '') || value === undefined) {
            window.alert(key + '不能为空！')
            return false
          } else if (value instanceof Array) {
            console(value)
            that.vail(value)
          } else {
            return true
          }
        })
        return true
      }
    }
  }
}
</script>



